*{
    margin: 0px ;
    padding: 0px;
    box-sizing: border-box;
}
.full-width-container {
    width: 100%;
    display: flex;
}

.sidebar {
    width: 18%;
    margin-left: 10px;
    padding-left: 10px;
    background-color: rgb(192, 191, 188);
}

.student-contents > h1 {
    margin-bottom: 1px;
    font-family: serif;
    text-align: center;
    margin-bottom: 1px;
}

.student-contents > h1 > div {
    margin-bottom: 15px;
}

.student-information > .information-field {
    padding: 5px;
    font-family: 'Arial Narrow', Arial, sans-serif;
}

.information-field > p {
    background-color: #fff;
    border-radius: 20px;
    padding: 5px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.4);
}

.student-credential-card {
    width: 225px;
    background-color: rgb(243, 242, 242);
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.4);
    margin-top: 1px;
    border-radius: 10px;
}

.student-side-by-side-bubble {
    display: flex;
    flex-wrap: wrap;
    font-size: 20px;
    justify-content: space-between;
}

.student-side-by-side-bubble > .bubble {
    margin: 8px;
    background-color: #fff;
    border-radius: 9px;
    padding: 5px;
    font-family: 'Segoe UI', Verdana, sans-serif;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
}

.bio-card {
    margin-top: 29px;
    width: 225px;
    background-color: rgb(243, 242, 242);
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
    border-radius: 10px;
}

.bio-card > h3 {
    text-align: center;
}

.bio-card-img > img {
    border-radius: 10px;
    margin: 5px;
    height: 80px;
    width: 80px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
    border: 1px solid black;
}

.bio-card-contents {
    display: flex;
    display: block;
    margin: 8px;
}

.bio-card-inside {
    display: flex;
    line-height: 25px;
}

label {
    font-size: 12px;
}

input {
    width: 40px;
}

.table-card {
    width: 248px;
    background-color: rgb(243, 242, 242);
    margin-top: 10px;
    border: 1px solid black;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
    margin-top: 30px;
    margin-right: 2px;
    margin-left: -5px;
    border-radius: 10px;
    text-align: center;
    padding-left: -1px;
}

.table-contents {
    border: 1px solid black;
}

table, th,td{
    border:1px solid black;
  }

.box {
    border: 1px solid black;
    padding: 0.3rem;
}

.box1 {
    border: 1px solid black;
    padding: 0.4rem;
}

.main-container{
    margin:0px 20px;
    width: 58%;
    height: 100%;
    background-color: rgb(192, 191, 188);
}

.student-card-2{
    box-shadow: 2px 1px 5px rgba(0,0,0,0.4);

    /* width: 470px; */
